@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserratmedium.woff2") format("woff2"), url("../fonts/montserratmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat.woff2") format("woff2"), url("../fonts/montserrat.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}
body {
  font-family: "Montserrat", "Arial", sans-serif;
  min-width: 320px;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.container {
  box-sizing: border-box;
  width: 320px;
  margin: 0 auto;
  padding-right: 15px;
  padding-left: 15px;
}
.container--mobile {
  width: 100%;
}
@media (min-width: 767px) {
  .container {
    box-sizing: border-box;
    width: 767px;
    margin: 0 auto;
    padding-right: 45px;
    padding-left: 45px;
  }
}
@media (min-width: 1023px) {
  .container {
    box-sizing: content-box;
    width: auto;
    margin: 0 auto;
  }
  .container--desktop {
    width: auto;
    margin: 0;
    padding: 0;
  }
}
@media (min-width: 1023px) and (min-width: 1365px) {
  .container {
    width: 1160px;
  }
}
.hidden {
  overflow: hidden;
}
.main-nav {
  margin-left: auto;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .main-nav {
    margin-left: 137px;
  }
}
@media (max-width: 767px) {
  .main-nav {
    margin-left: 0;
  }
  .main-nav--closed {
    display: none;
  }
}
.header-list {
  display: flex;
  justify-content: space-between;
  width: 594px;
  flex-wrap: wrap;
}
.header-list__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #F9FBFD;
  cursor: pointer;
}
.header-list__link:hover {
  color: tomato;
}
.header-list__item {
  margin-right: 33px;
}
.header-list__item:last-child {
  margin-right: 0;
}
.header-list__item:nth-child(4n+4) {
  margin-right: 0;
}
@media (max-width: 1023px) {
  .header-list__link {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
  }
  .header-list {
    width: 503px;
  }
  .header-list__item {
    margin-right: 23px;
  }
}
@media (max-width: 767px) {
  .header-list {
    display: block;
    width: auto;
  }
  .header-list__link {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #011C40;
  }
  .header-list__item {
    padding-bottom: 30px;
  }
  .header-list__item:last-child {
    padding-bottom: 65px;
  }
  .header-list__item:first-child {
    margin-left: 0;
  }
}
.page-header__wrapper {
  width: 100%;
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  padding-top: 35px;
}
.page-header__relative {
  position: relative;
}
.page-header__logo-wrapper {
  display: flex;
  align-items: center;
}
.page-header__logo-image--tablet {
  display: none;
}
.page-header__logo-image--mobile {
  display: none;
}
@media (max-width: 1023px) {
  .page-header__logo-image--desktop {
    display: none;
  }
  .page-header__logo-image--tablet {
    display: block;
  }
  .page-header__wrapper {
    width: 678px;
    padding-top: 30px;
    padding-left: 0;
  }
}
.page-header__wrapper--closed {
  background-color: transparent;
}
.page-header__wrapper--opened {
  background-color: #F9FBFD;
  left: 0;
  padding: 0 15px;
  height: 100%;
  position: fixed;
  box-sizing: border-box;
  z-index: 5;
}
.page-header__logo-image-blue--mobile--hidden {
  display: none;
}
.page-header__logo-image-blue--mobile--block {
  display: block;
}
@media (max-width: 767px) {
  .page-header__logo-image--tablet {
    display: none;
  }
  .page-header__logo-image--mobile {
    display: block;
  }
  .page-header__wrapper {
    width: 100%;
    display: block;
    padding-top: 20px;
  }
  .page-header__logo-wrapper {
    padding-bottom: 50px;
  }
}
.toggle {
  width: 24px;
  height: 14px;
  padding: 0;
  margin-left: auto;
  font-size: 0;
  background-color: transparent;
  border: none;
  background-repeat: no-repeat;
  outline: none;
}
.toggle--opened {
  background-image: url("../img/menu-cross.svg");
  width: 14px;
  height: 14px;
}
.toggle--closed {
  background-image: url("../img/menu-burger.svg");
}
@media (min-width: 767px) {
  .toggle {
    display: none;
  }
}
